import Action from '../actions/_actions'

const initialState = () => ({
    setting: [{
      id: 10003,
      title: '',
      totalCount: 3,
    },{
      id: 10002,
      title: '',
      totalCount: 2,
    },{
      id: 10001,
      title: '',
      totalCount: 1,
    }]
  }
);
const lotteryDrawing = (state = initialState(), action) => {
  switch (action.type) {
    case Action.SET_LOTTERY_SETTING:
      state.setting = action.lottery;
  }
  return Object.assign({}, state);
};

export default lotteryDrawing


